	module.exports = {"data":{"markdownRemark":{"html":"<p>These creatures are a form of orcs bred by the dreaded Magus, after the War of the Betrayal. They are weaker than normal Uruk-Hais, but more intelligent. They are known as Uruk-Lhuths, or spell-uruks.</p>\n<h2>Abilities</h2>\n<p>Uruk-Lhuth have an insane number of penalties, and are meant to be a challenge for the most elite of our players. First, they have no allies in the race war. Their main enemy are the good races of Arda, such as Elves and Hobbits, but they hate the Uruks, Common Orc, and Olog-Hais as well.</p>\n<p>Uruk-Lhuth may not practice any warrior skills, and have a VERY limited select of Ranger and Mystic art. However, they have a very large selection of magic spells to choose from, more so than any other evil race. They also do not have any shops. Uruk-Lhuth are resistant to poison.</p>\n<table class=\"table\">\n    <thead>\n        <tr>\n            <th>Strength</th>\n            <th>Intelligence</th>\n            <th>Will</th>\n            <th>Dexterity</th>\n            <th>Constitution</th>\n            <th>Learning Ability</th>\n        </tr>\n    </thead>\n    <tbody>\n        <tr>\n            <td>Low</td>\n            <td>Normal</td>\n            <td>Very Low</td>\n            <td>Normal</td>\n            <td>Normal</td>\n            <td>Low</td>\n        </tr>\n    </tbody>\n</table>","frontmatter":{"title":"Uruk-Lhuths","raceid":19}}},"pathContext":{"path":"/races/uruk-lhuths"}}

